.about-company__title {
  margin-bottom: 40px;
  text-align: center;
}
.about-company__desсription {
  margin-bottom: 18px;
  width: 100%;
  @include font(14px, 20px);
  color: #3e3e3e;
  &:last-child {
    margin-bottom: 0;
  }
}
.about-company__img-wrapper {
  margin-bottom: 18px;
}
.about-company__img {
  width: 100%;
}

.about-company-list {
  font-weight: 300;
  letter-spacing: 0.05em;
  @include font(14px, 20px);
}

.about-company-list-item {
  margin-bottom: 10px;
}

.button {
  display: block;
  margin: 0 auto;
  @include font(14px, 15px);
  font-weight: 500;
  padding: 12px 38px;
  border: none;
  border-radius: 9px;
  background-color: #f9963c;
  color: #fff;
}

@media screen and(min-width:768px) {
  .about-company .container {
    padding-left: 75px;
    padding-right: 75px;
  }
  .about-company__title {
    margin-bottom: 56px;
  }
  .about-company__desсription {
    margin-bottom: 40px;
    @include font(18px, 22px);
  }
  .about-company__desсription.mb {
    margin-bottom: 18px;
  }
  .about-company__img-wrapper {
    margin-bottom: 40px;
  }
  .button {
    @include font(16px, 15px);
    padding: 18px 54px;
  }
  .about-company-list {
    font-weight: 400;
    letter-spacing: 0.05em;
    @include font(18px, 22px);
  }
}

@media screen and(min-width:1440px) {
  .about-company .container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
    width: 1023px;
  }
  .about-company__title {
    margin-bottom: 84px;
  }
  .about-company__desсription {
    width: 544px;
    margin-left: 479px;
    font-weight: 300;
    letter-spacing: 0.05em;
    margin-bottom: 40px;
    @include font(18px, 22px);
  }
  .about-company-list {
    width: 544px;
    margin-left: 479px;
    font-weight: 300;
    letter-spacing: 0.05em;
    @include font(18px, 22px);
    // list-style-type: "\1F44D";
  }

  .about-company-list-item {
    margin-bottom: 10px;
  }
  // .about-company__desсription {
  //   margin-bottom: 18px;
  //   @include font(20px, 23.5px);
  // }
  .about-company__img-wrapper {
    left: 0px;
    position: absolute;
    top: 15%;
    //transform: translate(0, 50%);
  }
  .about-company__img-wrapper2 {
    left: 0px;
    position: absolute;
    top: 58%;
    //transform: translate(0, 50%);
  }
  .last-mb {
    margin-bottom: 22px;
  }

  .button {
    margin-left: 399px;
  }
}
